.task-list {
  background: #fff;
  border-radius: 6px;
  padding: 4px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.02);
  font-size: 0.6rem;
}

.task-list-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 4px;
}

.task-list-header h2 {
  font-size: 0.65rem;
  margin: 0;
}

.more-btn {
  background: none;
  border: none;
  font-size: 0.7rem;
  color: #aaa;
  padding: 0;
  margin: 0;
  line-height: 1;
}

.task-groups {
  display: flex;
  flex-direction: column;
  gap: 3px;
}

.task-group-header h4 {
  font-size: 0.6rem;
  margin: 0;
  color: #888;
}

.task-items {
  display: flex;
  flex-direction: column;
  gap: 2px;
}

.task-item {
  display: flex;
  align-items: center;
  gap: 4px;
}

.task-checkbox {
  width: 10px;
  height: 10px;
  border: 1px solid #ccc;
  border-radius: 2px;
  background: #fff;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
}

.task-checkbox.checked {
  background: #FFD93D;
  border-color: #FFD93D;
}

.checkmark {
  font-size: 7px;
  line-height: 1;
}

.task-title {
  font-size: 0.6rem;
  color: #333;
  margin: 0;
}

.task-date {
  font-size: 0.55rem;
  color: #aaa;
}
