.categories-widget {
  background: white;
  border-radius: 8px;
  padding: 0.75rem;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.06);
}

.categories-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.5rem;
}

.categories-header h3 {
  margin: 0;
  font-size: 0.75rem;
  font-weight: 600;
  color: #333;
}

.categories-list {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  margin-bottom: 0.5rem;
}

.category-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.3rem 0.4rem;
  border-radius: 4px;
  transition: background 0.2s ease;
}

.category-item:hover {
  background: #f8f8f8;
}

.category-info {
  display: flex;
  align-items: center;
  gap: 0.3rem;
}

.category-icon {
  width: 20px;
  height: 20px;
  font-size: 11px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.category-details {
  display: flex;
  flex-direction: column;
  gap: 0.05rem;
}

.category-name {
  font-size: 0.65rem;
  font-weight: 500;
  color: #333;
}

.category-count {
  font-size: 0.6rem;
  color: #777;
}

.category-members {
  display: flex;
  gap: 0;
}

.member-avatar {
  width: 18px;
  height: 18px;
  font-size: 0.6rem;
  border: 1px solid white;
  margin-left: -4px;
}

.member-avatar:first-child {
  margin-left: 0;
}

.add-more-btn {
  width: 100%;
  padding: 0.3rem;
  background: none;
  border: 1px dashed #ddd;
  border-radius: 4px;
  font-size: 0.65rem;
  color: #666;
  cursor: pointer;
  transition: all 0.2s ease;
}

.add-more-btn:hover {
  border-color: #FFD93D;
  color: #333;
  background: #fffce6;
}
