/* Layout wrapper */
.layout {
  display: flex;
  height: 100vh;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

/* Sidebar (fixed) */
.sidebar {
  width: 220px;
  background-color: #f9f9f9;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  border-right: 1px solid #e5e5e5;
}

/* Main area next to sidebar */
.layout-main {
  margin-left: 220px; /* match sidebar width */
  display: flex;
  flex-direction: column;
  flex: 1;
  height: 100vh;
  overflow: hidden;
}

/* Header (sticky at top of content) */
.header {
  height: 64px;
  background: white;
  border-bottom: 1px solid #e5e5e5;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 1rem;
  position: sticky;
  top: 0;
  z-index: 900;
}

/* Scrollable content under header */
.layout-content {
  flex: 1;
  overflow-y: auto;
  padding: 1rem;
  background-color: #f5f6f8; /* or your preferred dashboard background */
}

