.time-tracking {
  background: #fff;
  border-radius: 8px;
  padding: 10px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.07);
  font-size: 0.75rem;
}

.time-tracking-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 8px;
}

.time-tracking-header h3 {
  font-size: 0.85rem;
  margin: 0;
  font-weight: 600;
  color: #333;
}

.tracking-items {
  display: flex;
  flex-direction: column;
  gap: 6px;
}

.tracking-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 6px 8px;
  border-radius: 6px;
  transition: background 0.2s ease;
}

.tracking-item:hover {
  background: #f4f4f4;
}

.tracking-info {
  display: flex;
  align-items: center;
  gap: 6px;
  flex: 1;
}

.play-btn {
  width: 22px;
  height: 22px;
  font-size: 10px;
  background: #f0f0f0;
  border: none;
  border-radius: 50%;
  color: #555;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  padding: 0;
}

.play-btn:hover {
  background: #e0e0e0;
}

.play-btn.active {
  background: #FFD93D;
  color: #000;
}

.tracking-title {
  font-size: 0.75rem;
  color: #333;
}

.tracking-time {
  display: flex;
  align-items: center;
  gap: 6px;
}

.time-display {
  font-size: 0.7rem;
  color: #666;
  font-family: monospace;
}

.more-options {
  background: none;
  border: none;
  font-size: 0.75rem;
  color: #888;
  cursor: pointer;
  padding: 0;
}

.more-options:hover {
  color: #333;
}

@media (max-width: 768px) {
  .time-tracking {
    padding: 8px;
  }

  .tracking-item {
    padding: 6px;
  }
}
