@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600;700&display=swap');

:root {
  --yellow: #fee56c;
  --yellow-light: #fff6db;
  --grey-line: #eaeaea;
  --font: 'Inter', sans-serif;
}

body {
  font-family: var(--font);
  margin: 0;
  padding: 0;
  background: #f9f9f9;
  /* ðŸ”¥ Remove this line */
  /* overflow: hidden; */
}

.task-page-wrapper {
  padding: 1px 1px;
  width: 100%;
  /* ðŸ”¥ Remove this line */
  /* height: 100%; */
  box-sizing: border-box;
  display: grid;
  grid-template-rows: auto 1fr;
  /* ðŸ”¥ Remove this line */
  /* overflow: hidden; */
}
.tasks-wrapper {
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.05);
  padding: 10px;
  overflow-y: auto;
  height: 100%;
}
.tasks-section {
  margin-bottom: 10px;
}

.tasks-section h3 {
  font-size: 12px;
  font-weight: 600;
  padding: 4px 14px;
  margin: 0;
  color: #333;
}

.row {
  display: grid;
  grid-template-columns: 16px 1fr 70px 80px 65px 80px 30px;
  align-items: center;
   padding: 6px 14px;
  font-size: 11.5px;
  border-bottom: 1px solid var(--grey-line);
   min-height: 29px;
}

.row:last-child {
  border-bottom: none;
}

.row.head {
  font-size: 10px;
  font-weight: 600;
  letter-spacing: 0.03em;
  color: #868686;
  background: #fafafa;
  padding: 4px 14px;
}

.title {
  font-weight: 500;
  font-size: 11.5px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.badge {
  padding: 2px 6px;
  border-radius: 8px;
  font-size: 10.5px;
  font-weight: 500;
  text-align: center;
  white-space: nowrap;
}

.stage.in-progress {
  background: var(--yellow-light);
  color: #8d7424;
}

.stage.not-started {
  background: #f0f0f0;
  color: #8d8d8d;
}

.priority.high {
  background: #e8b374;
  color: #643600;
}

.priority.medium {
  background: var(--yellow);
  color: #5b5500;
}

.priority.low {
  background: #e3eefc;
  color: #1a57b8;
}

.due {
  font-weight: 500;
  font-size: 11px;
  color: #444;
}

.team {
  color: #626262;
  font-size: 10.5px;
}

.avatar {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  object-fit: cover;
}
