@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600&display=swap');

.notification-page {
  display: flex;
  width: 100%;
  height: 100%;
  font-family: 'Inter', sans-serif;
  gap: 16px; /* <-- space between left and right */
}

.notification-left {
  width: 32%;
  background: #fff;
  border-radius: 12px;
  padding: 12px;
  display: flex;
  flex-direction: column;
  box-shadow: 0 2px 6px rgba(0,0,0,0.06);
}

.notif-heading {
  font-size: 16px;
  font-weight: 600;
  margin-bottom: 10px;
}

.notif-list {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 8px;
  overflow: hidden;
}

.notif-card {
  display: flex;
  align-items: center;
  padding: 20px 25px;
  border-radius: 5px;
  background: #f5f5f5;
  cursor: pointer;
  width: 100%;
  box-sizing: border-box;
}


.notif-card.active {
  background: #fdf6e3;
  border-left: 4px solid #facc15;
}

.notif-icon {
  margin-right: 8px;
}

.circle {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: #ccc;
  display: inline-block;
}

.checkmark {
  font-size: 14px;
  color: #4caf50;
}

.notif-content {
  flex: 1;
}

.notif-title {
  font-weight: 600;
  font-size: 14px;
}

.strike {
  text-decoration: line-through;
  color: #999;
}

.notif-msg {
  font-size: 12px;
  color: #666;
}

.notif-avatar {
  width: 28px;
  height: 28px;
  border-radius: 50%;
  margin-left: 10px;
}

.notification-right {
  width: 68%;
  background: #fff;
  border-radius: 12px;
  padding: 12px;
  box-shadow: 0 2px 6px rgba(0,0,0,0.06);
  display: flex;
  flex-direction: column;
}

.task-title-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.task-heading {
  font-size: 18px;
  font-weight: 600;
}

.task-close {
  font-size: 18px;
  cursor: pointer;
  color: #999;
}

.task-detail {
  display: flex;
  justify-content: space-between;
  font-size: 14px;
  margin-top: 8px;
}

.pill {
  font-size: 12px;
  padding: 4px 10px;
  border-radius: 20px;
  background: #f0f0f0;
}

.pill.dark {
  background: #d6e0ff;
}

.pill.light {
  background: #e4fbe9;
}

.pill.yellow {
  background: #fff3cd;
}

.section-box {
  margin-top: 18px;
}

.section-box h4 {
  font-size: 14px;
  margin-bottom: 6px;
}

.section-row {
  display: flex;
  justify-content: space-between;
  font-size: 13px;
}

.section-row button {
  padding: 5px 10px;
  background: #333;
  color: white;
  border: none;
  border-radius: 6px;
  font-size: 12px;
  cursor: pointer;
}

.task-actions {
  display: flex;
  gap: 10px;
  margin-top: 20px;
}

.btn {
  flex: 1;
  padding: 8px;
  font-size: 13px;
  border-radius: 8px;
  cursor: pointer;
}

.btn.outline {
  border: 1px solid #aaa;
  background: #fff;
  color: #333;
}

.btn.danger {
  background: #f44336;
  border: none;
  color: white;
}

.task-actions {
  display: flex;
  gap: 10px;
  margin-top: auto; /* ðŸ‘ˆ THIS is the key line */
}

