.profile-wrapper {
  padding: 2rem;
  font-family: 'Inter', sans-serif;
  background: #fafafa;
  min-height: 100vh;
}

.profile-title {
  font-size: 24px;
  font-weight: 700;
  margin-bottom: 1.5rem;
}

.profile-card {
  background: #fff;
  border-radius: 16px;
  padding: 32px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.08);
  display: flex;
  flex-direction: column;
  gap: 32px;
}

/* Two column layout for each row */
.profile-row-grid {
  display: grid;
  grid-template-columns: 180px 1fr;
  align-items: flex-start;
  gap: 32px;
}

/* Section heading on the left */
.profile-section-heading {
  font-size: 15px;
  font-weight: 600;
  color: #111;
  padding-top: 6px;
}

/* Profile Photo Block */
.profile-photo-block {
  display: flex;
  gap: 24px;
  align-items: center;
}

.profile-photo {
  width: 96px;
  height: 96px;
  object-fit: cover;
  border-radius: 12px;
  background: #f1f1f1;
}

/* Upload */
.upload-btn {
  padding: 8px 16px;
  border-radius: 20px;
  background: #fff;
  border: 1px solid #ccc;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
}

.upload-btn:hover {
  background: #f4f4f4;
}

.upload-info {
  font-size: 12px;
  color: #777;
  margin-top: 6px;
}

/* Contact input field */
.profile-input-block {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.profile-label {
  font-size: 13px;
  font-weight: 500;
  color: #111;
}

.profile-input {
  padding: 6px 8px;
  font-size: 12px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background: #f5f5f5;
  width: 260px;
}
.profile-input:focus {
  outline: 2px solid #1582d1;
}

/* Email section */
.email-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.email-value {
  font-size: 14px;
  color: #1582d1;
  font-weight: 500;
}

.change-email-btn {
  padding: 8px 16px;
  border-radius: 20px;
  background: #fff;
  border: 1px solid #ccc;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
}

.change-email-btn:hover {
  background: #f2f2f2;
}

/* Centered action buttons */
.action-buttons-centered {
  display: flex;
  justify-content: center;
  gap: 16px;
  margin-top: 24px;
}

.cancel-btn,
.save-btn {
  padding: 12px 24px;
  font-size: 14px;
  font-weight: 500;
  border-radius: 24px;
  cursor: pointer;
  transition: 0.2s ease;
}

.cancel-btn {
  background: #fff;
  border: 1px solid #ccc;
}

.cancel-btn:hover {
  background: #f5f5f5;
}

.save-btn {
  background: #ffe46c;
  border: none;
  color: #000;
  font-weight: 600;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.save-btn:hover {
  background: #fddc60;
}
